import { el } from "@webtaku/el";
import "./layout.css"

export class Layout {
  header: HTMLElement;
  contentContainer: HTMLElement;
  drawer: HTMLElement;

  constructor() {
    // 브랜드(좌측), 네비게이션(중앙/우측), 모바일 토글(우측)
    const brandLink = el('a.brand',
      el('img', { src: 'data:image/png;base64,' }),
      'Kaia NFT Marketplace', { href: '/' });

    const nav = el('nav.primary-nav',
      el('sl-button', '탐색', { variant: 'text', size: 'large', className: 'nav-btn', 'data-key': 'explore' }),
      el('sl-button', '내 NFT', { variant: 'text', size: 'large', className: 'nav-btn', 'data-key': 'my-nft' }),
      el('sl-button', '히스토리', { variant: 'text', size: 'large', className: 'nav-btn', 'data-key': 'history' })
    );

    const menuToggle = el('sl-icon-button#menuToggle', {
      name: 'list',
      label: '메뉴 열기',
      className: 'menu-toggle'
    });

    // 모바일 드로어 + 메뉴
    this.drawer = el('sl-drawer#mobileNav', { label: '메뉴', placement: 'start' },
      el('sl-menu',
        el('sl-menu-item', '탐색', { 'data-key': 'explore' }),
        el('sl-menu-item', '내 NFT', { 'data-key': 'my-nft' }),
        el('sl-menu-item', '히스토리', { 'data-key': 'history' }),
      )
    ) as HTMLElement;

    // 헤더 컨테이너
    this.header = el('header.site-header',
      el('div.inner',
        brandLink,
        nav,
        menuToggle
      ),
    );

    // 이벤트: 모바일 아이콘 → 드로어 open
    (menuToggle as any).addEventListener('click', () => {
      (this.drawer as any).open = true;
    });

    // 이벤트: 드로어 메뉴 클릭 → 드로어 close (원한다면 라우팅 연결)
    this.drawer.addEventListener('sl-select', (e: any) => {
      const key = e.detail?.item?.dataset?.key;
      // 여기에서 key(explore | my-nft | history)에 맞는 라우팅/액션 수행
      (this.drawer as any).hide();
    });

    // 데스크톱 nav 버튼 클릭 처리 (필요 시 라우팅 연결)
    nav.querySelectorAll('.nav-btn').forEach(btn => {
      btn.addEventListener('click', (e) => {
        const key = (e.currentTarget as HTMLElement).dataset.key;
        // 라우팅/액션 처리 지점
        console.log('navigate:', key);
      });
    });

    this.contentContainer = el('div.content-container');
  }

  mountContent(content: HTMLElement) {
    this.contentContainer.replaceChildren(content);
  }
}